.list {
  display: flex;
  flex-flow: row wrap;
  list-style-type: none;
  margin: 0;
  padding: 0;
  height: 90%;
  width: 100%; }
  .list li {
    display: flex;
    border: 1px solid black;
    flex-grow: 1;
    align-items: center;
    font: 1rem sans;
    font-weight: 500; }
    .list li a {
      text-decoration: none;
      text-align: center;
      color: black;
      padding: .6em; }
    .list li:first-child {
      border-bottom-left-radius: 4px;
      border-top-left-radius: 4px;
      background: black; }
      .list li:first-child a {
        color: white; }
    .list li:last-child {
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px; }

.list:last-child li a.arrow:before {
  content: '\f061'; }

.list:last-child li a.search:before {
  content: '\f002'; }

.page-wrap {
  padding-top: 0.75em;
  display: flex;
  margin: auto;
  width: 90%;
  min-height: 90vh;
  flex-flow: column;
  justify-content: space-around; }
  .page-wrap * {
    flex-grow: 1; }

.page-main {
  flex: 1; }
